html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0; }

html {
  font-size: 62.5%;
  background-image: url(../img/fon.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed; }

body {
  font-family: 'Barlow Semi Condensed', sans-serif; }

a {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul, li {
  list-style: none;
  margin: 0;
  padding: 0; }

#particles-js {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: -1; }

.container {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center; }
  .container__description {
    font-size: 3rem;
    padding-top: 1em;
    color: #1B4367;
    transition: 3s all; }
    .container__description--shift {
      transform: translateY(100px);
      opacity: 0; }
  .container__name {
    padding-top: 2em; }
  .container__img .img-my-photo {
    padding: 5rem;
    border-radius: 50%;
    width: 25%; }

.running-string {
  font-family: 'Source Serif Pro', serif;
  font-size: 6rem;
  color: #1B4367; }

.list-item {
  display: inline-block;
  text-transform: uppercase;
  font-size: 2rem;
  padding: 1em; }

.item-link {
  color: #1B4367; }

hr {
  background: #0BAAD4; }

.item-link, .item-link > span {
  position: relative;
  color: inherit;
  text-decoration: none;
  line-height: 24px; }
  .item-link:before, .item-link:after, .item-link > span:before, .item-link > span:after {
    content: '';
    position: absolute;
    transition: transform .5s ease; }

.item-link {
  transition: 0.2s linear all;
  display: inline-block;
  overflow: hidden; }
  .item-link:before, .item-link:after {
    right: 0;
    bottom: 0;
    background: #0BAAD4; }
  .item-link:before {
    width: 100%;
    height: 2px;
    transform: translateX(-100%); }
  .item-link:after {
    width: 2px;
    height: 100%;
    transform: translateY(100%); }
  .item-link:hover {
    color: #0BAAD4; }
  .item-link:active {
    color: green; }
  .item-link > span {
    display: block;
    padding: 10px; }
    .item-link > span:before, .item-link > span:after {
      left: 0;
      top: 0;
      background: #0BAAD4; }
    .item-link > span:before {
      width: 100%;
      height: 2px;
      transform: translateX(100%); }
    .item-link > span:after {
      width: 2px;
      height: 100%;
      transform: translateY(-100%); }
  .item-link:hover:before, .item-link:hover:after, .item-link:hover > span:before, .item-link:hover > span:after {
    transform: translate(0, 0); }
